import React from 'react'
import './achievement.scss'

function Achievement() { 
    return ( 
        <div className="container"> 
            <div className="award"> 
                <div className="d-flex mb-3"> 
                    <img src="/static/images/award.png" alt="logo"/>  
                    <h1>Achievements</h1> 
                </div> 
                <h3 className="mb-3">Academic</h3> 
                <p className="dean"> 
                    <strong>Dean Certificate - </strong> Based on academic performance - Spring Semester 2016 - Green University 
                </p> 

                <h3>Programming Contest</h3> 
                <p><strong>Ranked 1st</strong> - GUB IPC</p> 
            </div> 
        </div> 
    ) 
} 

export default Achievement; 
